.CharComponent {
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.CharComponent span {
  font-size: 30px;
  font-weight: bold;
  -webkit-transition: margin .1s linear;
  -o-transition: margin .1s linear;
  transition: margin .1s linear;
}

.CharComponent:hover span {
  color: red;
  margin: 0 5px;
}



